import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom"; // Add this
import "./App.scss";
import Main from "./containers/Main";
import FinancialDashboardDetail from "./pages/FinancialDashboardDetail"; // Import your new page

function App() {
  return (
    <Router>
      <Switch>
        <Route exact path="/" component={Main} />
        <Route path="/financial-dashboard" component={FinancialDashboardDetail} />
      </Switch>
    </Router>
  );
}

export default App;

