import React, { useContext } from "react";
import Headroom from "react-headroom";
import "./Header.scss";
import ToggleSwitch from "../ToggleSwitch/ToggleSwitch";
import StyleContext from "../../contexts/StyleContext";
import {
  greeting,
  workExperiences,
  resumeSection,
  bigProjects //  changed from projectsSection to bigProjects
} from "../../portfolio";

function Header() {
  const { isDark } = useContext(StyleContext);
  const viewExperience = workExperiences.display;
  const viewResume = resumeSection.display;
  const viewProjects = bigProjects.title || bigProjects.display; //  updated reference

  return (
    <Headroom>
      <header className={isDark ? "dark-menu header" : "header"}>
        <a href="/" className="logo">
          <span className="grey-color"> &lt;</span>
          <span className="logo-name">{greeting.username}</span>
          <span className="grey-color">/&gt;</span>
        </a>

        <input className="menu-btn" type="checkbox" id="menu-btn" />
        <label
          className="menu-icon"
          htmlFor="menu-btn"
          style={{ color: "white" }}
        >
          <span className={isDark ? "navicon navicon-dark" : "navicon"}></span>
        </label>

        {/*  Simplified and reordered navigation items */}
        <ul className={isDark ? "dark-menu menu" : "menu"}>
          {viewProjects && (
            <li>
              <a href="#projects">Projects</a>
            </li>
          )}

          {viewExperience && (
            <li>
              <a href="#experience">Experience</a>
            </li>
          )}

          {viewResume && (
            <li>
              <a href="#resume">Resume</a>
            </li>
          )}

          <li>
            <a href="#contact">Contact</a>
          </li>

          <li>
            {/* Dark/Light mode toggle remains unchanged */}
            {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
            <a className="theme-toggle">
              <ToggleSwitch />
            </a>
          </li>
        </ul>
      </header>
    </Headroom>
  );
}

export default Header;
